import React, { useEffect } from 'react';
import { Link } from 'gatsby';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowDown } from '@fortawesome/free-solid-svg-icons';
import { init } from 'ityped';

import landingStyles from './landing-styles.module.scss';

const Landing = () => {
  useEffect(() => {
    const myElement = document.querySelector('#myElement');
    const typer = [
      'I’m an engineer turned  full-stack web developer',
      'Maltese born, currently based in Nottingham.',
      'I build things on the web',
    ];
    const typerOptions = {
      strings: typer,
      showCursor: true,
      cursorChar: '|',
      typeSpeed: 70,
      backDelay: 2500,
      startDelay: 1000,
    }
    init(myElement, typerOptions);
  });

  return (
    <section className={landingStyles.wrapper}>
      <div className={landingStyles.container}>
        <div className={landingStyles.content} id="content">
          <h2 className={`heading-xl`}>Hi, I'm James</h2>
          <p className={`paragraph-lg ${landingStyles.display}`}><span id="myElement" /></p>
          <div className={landingStyles.down}>
            <Link to="#portfolio" id="view-more">
              <FontAwesomeIcon icon={faArrowDown} className={`${landingStyles.arrow} ${landingStyles.bounce}`} />
              View Selected Work
            </Link>
          </div>
        </div>
        {/* <div className={landingStyles.social}>
          <a href="https://github.com/James-Coates">
            <FontAwesomeIcon icon={faGithub} />
          </a>
          <a href="https://github.com/James-Coates">
            <FontAwesomeIcon icon={faTwitter} />
          </a>
          <a href="https://github.com/James-Coates">
            <FontAwesomeIcon icon={faLinkedinIn} />
          </a>
          <a href="https://github.com/James-Coates">
            <FontAwesomeIcon icon={faMediumM} />
          </a>
        </div> */}
      </div>
    </section>
  );
};

export default Landing;
